import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import routeConstant from 'routes/route/routeConstant';
import { Link } from 'react-router-dom';
import { Card, Row, Col, Button } from 'antd';
import { TableData, CartHeadAction } from 'components/common';
import { BtnExport } from 'components/common/button';
import {
  getStudentsForUnionsMem,
  updateUnionDate,
  exportUnionStudents,
} from 'services/studentForUnionsMem';
import { getQueryParams } from 'utils/urlUtil';
import { dateToString } from 'utils/formatUtil';
import FormSearchStuForUnionsMem from './FormSearchStuForUnionsMem';
import './style.scss';

class StuUnionsMemPage extends Component {
  state = {
    reload: 0,
    paramSearch: {},
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  handleKhaiTru = async ({ id, unionDate }) => {
    try {
      await updateUnionDate({ id, unionDate });
      this.setState({ reload: this.state.reload + 1 });
    } catch (error) {
      console.log(error);
    }
  };

  handleExport = async ({
    trainTypeId,
    facultyId,
    courseId,
    studyFieldId,
    classId,
    studentCode,
    studentFullName,
  }) => {
    try {
      await exportUnionStudents({
        trainTypeId,
        facultyId,
        courseId,
        studyFieldId,
        classId,
        studentCode,
        studentFullName,
      });
    } catch (error) {
      console.log(error);
    }
  };

  columns = [
    {
      title: 'M SV',
      dataIndex: 'code',
      key: 'code',
    },
    {
      title: 'H tn',
      dataIndex: 'fullname',
      key: 'fullname',
    },
    {
      title: 'Ngy sinh',
      dataIndex: 'dob',
      key: 'dob',
    },
    {
      title: 'Gii tnh',
      dataIndex: 'genderText',
      key: 'genderText',
    },
    {
      title: 'Tn lp',
      dataIndex: 'className',
      key: 'className',
    },
    {
      title: 'Chuyn ngnh',
      dataIndex: 'studyFieldName',
      key: 'studyFieldName',
    },
    {
      title: 'Kha hc',
      dataIndex: 'courseName',
      key: 'courseName',
    },
    {
      title: 'Tn khoa',
      dataIndex: 'facultyName',
      key: 'facultyName',
    },
    {
      title: 'Tn h',
      dataIndex: 'trainTypeName',
      key: 'trainTypeName',
    },
    {
      title: 'Ngy kt np',
      dataIndex: 'unionDate',
      render: (text, record) => {
        if (record.unionDate == null) {
          return <Fragment />;
        }
        return <Fragment>{dateToString(record.unionDate, 'DD/MM/YYYY')}</Fragment>;
      },
    },
    {
      title: 'Thao tc',
      dataIndex: 'operation',
      render: (text, record) => {
        if (record.unionDate) {
          return (
            <Fragment>
              <Button
                className="btnEventStuUnionsMem btnKhaiTru"
                type="primary"
                onClick={() => this.handleKhaiTru({ id: record.id, unionDate: '' })}
              >
                Khai tr
              </Button>
            </Fragment>
          );
        }
        return (
          <Link
            to={{
              pathname: routeConstant.DOAN_HOI__DOAN_VIEN__NHAP_DU_LIEU,
              search: `?id=${record.id}`,
            }}
          >
            <Button className="btnEventStuUnionsMem btnKetNap" type="primary">
              Kt np
            </Button>
          </Link>
        );
      },
    },
  ];

  render() {
    const { paramSearch } = this.state;
    console.log('paramSearch: ', paramSearch);
    return (
      <Card title="on vin l sinh vin">
        <CartHeadAction>
          <BtnExport
            title="Xut Excel"
            onClick={() =>
              this.handleExport({
                trainTypeId: paramSearch.trainTypeId,
                facultyId: paramSearch.facultyId,
                courseId: paramSearch.courseId,
                studyFieldId: paramSearch.studyFieldId,
                classId: paramSearch.classId,
                studentCode: paramSearch.studentCode,
                studentFullName: paramSearch.studentFullName,
              })
            }
          />
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <FormSearchStuForUnionsMem />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              paramSearch={paramSearch}
              service={getStudentsForUnionsMem}
              allowGetData={
                !!paramSearch.trainTypeId &&
                !!paramSearch.facultyId &&
                !!paramSearch.courseId &&
                !!paramSearch.studyFieldId
              }
              columns={this.columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <StuUnionsMemPage />
  </Page>
);



